import { Fragment } from "react";
import { SRLWrapper } from "simple-react-lightbox";

import Header from "../components/headers";
import Loader from "../../components/loader";
import Footer from "../components/footer";
import Empty from "../error/empty";

const Gallery = ({
  brand,
  navLinks,
  headerImage,
  headerTitle,
  data,
  isloading,
}) => {
  const imageTiles = data.map((tile) => (
    <div key={tile.id} style={{ maxHeight: 300, overflow: 'hidden' }} className="col-md-6 col-lg-4 my-2">
      <SRLWrapper>
        <img className="img-fluid" src={tile.imageUrl} alt={tile.name} />
      </SRLWrapper>
    </div>
  ));
  const isData = data?.length ? imageTiles : <Empty />;
  return (
    <Fragment>
      <Header
        brand={brand}
        navItems={navLinks}
        image={headerImage}
        title={headerTitle}
        height={"40vh"}
        center
      />
      <div className="container my-3">
        <div className="row"> {isloading ? <Loader /> : isData} </div>
      </div>
      <Footer navItem={navLinks} />
    </Fragment>
  );
};

Gallery.defaultProps = {
  data: [],
};

export default Gallery;
