import { Component } from "react";
import { connect } from "react-redux";

import View from "../../presentations/gallery";
import slide2 from "../../assets/nurse.jpg";
import {
  getNavBrandState,
  getNavLinkState,
  getGalleryState,
  getGalleryLoader,
} from "../../store/reselect";
import { getGalleryAction } from "../../store/action";

class GalleryContainer extends Component {
  state = {};

  componentDidMount() {
    this.props.getGallery()
  }

  render() {
    let {
      props: { brand, navlinks, gallery, loader },
    } = this;
    return (
      <View
        brand={brand}
        navLinks={navlinks}
        headerImage={slide2}
        headerTitle="Gallery"
        data={gallery}
        isloading={loader}
      />
    );
  }
}

const mapStateToProps = (state) => {
  return {
    brand: getNavBrandState(state),
    navlinks: getNavLinkState(state),
    gallery: getGalleryState(state),
    loader: getGalleryLoader(state)
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getGallery: () => dispatch(getGalleryAction()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(GalleryContainer);
